<template>
  <div id="app">
    <div
      class="top"
      ref="top"
    >
      <h1>vue-base</h1>
      <div class="links">
        <router-link
          class="link"
          tag="li"
          to='/todoList'
        >
          <a href="javascript:;">todoList</a>
        </router-link>
        <router-link
          class="link"
          tag="li"
          to='/firstProject'
        >
          <a href="javascript:;">firstProject</a>
        </router-link>
      </div>
    </div>
    <keep-alive>
      <router-view />
    </keep-alive>
  </div>
</template>

<script>

export default {
  name: 'app',
  data() {
    return {
      // routers: ['todoList', 'firstProject','meishi']
      routers: ['todoList', 'firstProject']
    }
  },
  components: {
  },

  mounted() {
    this.showTop()
  },
  methods: {

    showTop() {
      let top = this.$refs.top
      document.addEventListener('mousemove', (e) => {
        let y = e.clientY,
          distance = 30
        if (y < distance) {
          top.style.transform = 'translateX(-50%) translateY(0%)'
        }
      })
      top.addEventListener('mouseover', () => {
        top.style.transform = 'translateX(-50%) translateY(0%)'
      })
      top.addEventListener('mouseleave', () => {
        top.style.transform = 'translateX(-50%) translateY(-100%)'
      })


    },



  }
}
</script>

